import React, { useEffect, useState } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCopyright } from '@fortawesome/free-regular-svg-icons';
import { ParallaxProvider } from 'react-scroll-parallax';

import Project from './Project';
import Header from './Header';
import Info from './Info';
import Vaardigheden from './Vaardigheden';
import Contact from './Contact';

const App = () => {

    const [projects, setProjects] = useState([]);

    useEffect(() => {
        fetch('projects.json')
            .then(response => response.json())
            .then(data => setProjects(data.projects));
    }, []);

    return (
        <ParallaxProvider>
            <div id='app'>
                <div></div>
                <div id='app-inner'>
                    <Header/>
                    <Info />
                    <div id='vaardigheden' className='title'>Vaardigheden</div>
                    <hr />
                    <Vaardigheden />
                    <div id='projecten' className='title'>Mijn projecten</div>
                    <hr />
                    {projects.map((project, key) => <Project project={project} key={key} />)}
                    <div id='contact' className='title'>Contact</div>
                    <hr />
                    <Contact />
                    <div id='footer'>
                        <FontAwesomeIcon icon={faCopyright}/>
                        {new Date().getFullYear()} Lucas Bos
                    </div>
                </div>
                <div></div>
            </div>
        </ParallaxProvider>
    )
};

export default App;